@media screen and (max-width: 640px) {
  //GRID
  .container {
    padding: 0;
    .video-col-mob {
      padding: 0; } }

  //HEADER
  #header {
    background-size: auto 100% !important;
    max-height: 350px;
    .top-bar {
      ul {
        .logo {
          width: 90px;
          height: 41px;
          a {
            width: 90px;
            height: 41px;
            background-size: 90px 41px; } } } }
    .quote {
      padding: 140px 0 0 0;
      margin: 0;
      width: auto;
      h1 {
        width: 90%;
        margin: 0 auto;
        border: none;
        text-align: center;
        padding-left: 0;
        font-size: 30px;
        line-height: 30px; }
      p {
        font-size: 15px;
        text-align: center; }
      hr {
        width: 15%;
        margin: 0 auto;
        border: solid #fff;
        border-width: 3px 0 0; } } }
  //EXTERNAL
  .external #header .quote h1 {
    line-height: 30px;
    margin-bottom: 15px; }
  .external {
    .container {
      height: 100%; }
    h2 {
      font-size: 18px; }
    #header {
      background-size: auto 100% !important;
      height: 50vh;
      .quote {
        position: relative;
        top: 15vh;
        margin-top: 0;
        padding-top: 0;
        float: none; } }
 }    //MAIN
  .page-template-about_us {
    section#two-columns {
      padding: 60px 15px;
      h2 {
        margin-bottom: 14px; }
      .text {
        text-align: justify;
        p {
          margin-bottom: 0; } }
      .columns {
        padding: 0; } }
    section#staff {
      padding: 50px 0;
      h2 {
        width: 80%;
        margin: 0 auto;
        font-size: 18px;
        line-height: 20px;
        margin-bottom: 5px; } }
    section#video {
      iframe {
        height: auto;
        display: block; } }
    section#history {
      margin-top: 0; }
    section#staff {
      .staff-list {
        .person {
          display: table; }
        .user-photo {
          display: table-cell; }
        .user-info {
          vertical-align: middle;
          display: table-cell;
          text-align: left;
          .name {
            margin-top: 0!important; } } } }
    section#statuts {
      height: auto;
      padding: 54px 15px 60px;
      h2 {
        margin: 0 0 31px; }
      p {
        margin-bottom: 0; }
      .owl-controls {
        .owl-pagination {
          position: relative;
          bottom: -25px; } }
      .owl-carousel {
        .owl-item {
          padding-left: 0; } } }
    section#history {
      background: none!important; }
    section#history-mobile {
      background-image: url("../img/external/history-mobile-bg.jpg");
      background-size: 100% auto;
      padding: 50px 15px 0;
      color: white;
      font-family: 'Open Sans', sans-serif;
      font-size: 15px;
      font-weight: 400;
      line-height: 20px;
      h2 {
        color: white; }
      h3 {
        font-family: 'Open Sans', sans-serif;
        color: white;
        font-size: 20px;
        font-weight: 300;
        line-height: 13px;
        padding: 5px 0; }
      .history-mobile-carousel {
        padding-top: 20px; }
      .owl-wrapper-outer {
        width: 80%;
        margin: 0 auto; }
      .owl-carousel {
        .owl-item {
          padding: 0; } }
      .owl-controls {
        .owl-buttons {
          display: block;
          top: 45%;
          div {
            width: 15px;
            height: 30px;
            opacity: 0.5; }
          .owl-prev {
            left: 0px;
            background-size: 15px 30px; }
          .owl-next {
            right: 0px;
            background-size: 15px 30px; } } } }
    section#rules {
      height: auto;
      padding-top: 65px;
      .document-list {
        margin-top: 40px;
        .row {
          border: none;
          padding: 0;
          &:first-child {
            border: none; }
          a {
            width: 48%;
            font-size: 12px;
            padding: 0 0 12px 30px; } } }
      .owl-controls {
        .owl-pagination {
          bottom: -35px; } } } }
  .page-template-businnes {
    #header {
      background-size: auto 100% !important;
      height: 35vh; }
    section#gallery {
      height: auto;
      padding: 66px 20px 20px;
      h2 {
        margin-bottom: 40px; }
      .owl-carousel {
        .owl-item {
          padding: 0 5px; } } }
    section#two-columns {
      padding: 60px 15px;
      h2 {
        font-size: 18px;
        font-weight: 500;
        line-height: 25px;
        margin-bottom: 22px; }
      .text {
        max-width: 100%;
        text-align: justify;
        font-size: 15px;
        line-height: 20px;
        p {
          margin: 0; } } }
    .simple {
      padding: 55px 15px 78px;
      h2 {
        line-height: 25px;
        margin-bottom: 22px; }
      .text {
        text-align: justify;
        font-size: 13px; }
      img {
        margin-top: 44px; } }
    #convention_first {
      padding-top: 55px;
      h2 {
        width: 80%;
        margin: 0 auto;
        line-height: 25px;
        display: block;
        padding-bottom: 22px; } } }
  .page-template-sub-project {
    section.icon-list {
      a {
        margin: 0;
        float: left;
        width: 33%;
        div {
          font-size: 11px; }
        .icon-wrapper {
          width: 75px;
          height: 50px;
          text-align: center;
          margin: 0 auto 15px; } } }
    section#two-columns {
      background-color: #f0f1f1;
      padding: 50px 15px 0px;
      h2 {
        margin-bottom: 22px; }
      .text {
        text-align: justify;
        p {
          margin-bottom: 0; } } }
    section.icon-list {
      background-color: #f0f1f1;
      padding: 50px 15px 100px;
      margin: 0; }
    section#preview {
      background: none;
      padding: 0px 15px 50px;
      text-align: center;
      .preview-mob-text {
        padding: 50px 0 20px;
        text-align: justify;
        h2 {
          font-size: 18px;
          font-weight: 500;
          line-height: 25px;
          margin-bottom: 20px; }
        p {
          font-size: 15px;
          line-height: 20px; } }
      .visit-site {
        display: inline-block;
        color: #7c8589;
        font-size: 14px;
        font-weight: 400;
        line-height: 40px;
        border: 2px solid #7c8589;
        border-radius: 10px;
        padding: 0 15px;
        min-width: 161px; }
      .browser {
        background-size: contain;
        height: auto;
        background-repeat: no-repeat;
        img {
          margin: 0;
          padding: 14px 10px 10px; } } } }



  //CONTENT
  .single-post {
    p {
      color: #6f7172;
      font-size: 16px;
      font-weight: 400;
      line-height: 17px; } }
  .home {
    #video {
      height: auto;
      .video-link {
        height: auto;
        background-size: cover;
        position: relative;
        padding-top: 200px;
        .go-to-video {
          position: absolute;
          left: 0;
          right: 0;
          top: 50%;
          transform: translateY(-50%); } }
      .video-description {
        padding: 45px 5px;
        h2 {
          font-size: 18px;
          border: none;
          text-align: center;
          font-weight: 500; }
        .description {
          text-align: justify;
          p {
            font-size: 15px;
            margin-bottom: 0; } } } }

    #business {
      margin-top: 0;
      padding-bottom: 40px;
      .container {
        .shadow {
          display: none; }
        .wrap {
          padding: 0;
          .columns.specification {
            height: auto;
            a {
              text-align: center;
              padding-bottom: 0;
              .icon {
                width: 76px!important;
                height: 34px!important;
                background-size: contain!important;
                background-position: center!important;
                display: inline-block; }
              .title {
                font-size: 11px;
                margin-top: 20px; } } }
          .about {
            padding-top: 65px;
            h2 {
              width: auto;
              font-size: 18px;
              text-align: center;
              border: none;
              padding: 0;
              font-weight: 500; }
            .description {
              padding: 0 15px;
              text-align: justify;
              p {
                font-size: 15px; } } } } } }
    #news {
      height: auto;
      padding: 0 15px 50px 0;
      background-color: #f0f1f1;
      .container {
        padding: 75px 0 0;
        .news {
          width: auto;
          .title {
            padding: 0;
            font-size: 14px;
            line-height: 19px; }
          .date {
            padding-left: 0; }
          .description {
            padding: 0;
            p {
              margin-bottom: 15px; } } }
        h2 {
          width: auto;
          font-size: 18px;
          text-align: center;
          border: none;
          padding: 0;
          font-weight: 500; } } } }
  //CAROUSEL
  .owl-controls {
    .owl-buttons {
      display: none; }
    .owl-pagination {
      position: relative;
      bottom: 0px; } }
  .owl-carousel {
    .owl-item {
      padding-left: 15px; } }




  //FOOTER
  #footer {
    height: auto;
    .container {
      .logo {
        width: 90px;
        height: 40px;
        background-size: 90px 40px; }
      .footer-top {
        background-color: #747474;
        text-align: center;
        padding: 20px 0;
        margin-top: 30px; }
      .go-top {
        display: inline-block;
        color: white;
        font-family: 'Open Sans', sans-serif;
        font-size: 20px;
        font-weight: 300;
        line-height: 30px;
        position: relative;
        padding-top: 50px;
        &:before {
          content: "";
          width: 44px;
          height: 40px;
          background-image: url("../img/vector/footer-top.svg");
          background-size: 44px 40px;
          position: absolute;
          top: 0;
          left: 50%;
          transform: translateX(-50%); } }

      .footer-menu {
        display: block;
        list-style: none;
        text-align: justify;
        margin: 0 0 25px;
        padding: 0 5px;
        li {
          float: left;
          text-align: left;
          width: 33%;
          a {
            color: white;
            font-family: 'Open Sans', sans-serif;
            font-size: 11px;
            font-weight: 400;
            line-height: 11px;
            padding: 0 5px; } } }
      .top {
        margin-bottom: 25px;
        .social-links {
          margin: 0;
          top: 0;
          li {
            float: left;
            width: 40px;
            height: 40px;
            margin: 0 8px 0 0;
            border: 2px solid white;
            text-align: center;
            a {
              position: relative;
              top: 50%;
              transform: translateY(-50%); } } }
        .acreditation {
          padding: 9px 10px;
          font-size: 11px;
          float: left!important; } } }
    .contact-info {
      p {
        margin-bottom: 0;
        color: #fff;
        font-size: 11px; } } }
  //RESPONSIVE MENU
  .title-bar {
    background-color: rgba(0, 34, 72, 0.6);
    border: none;
    height: 60px;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 20;
    padding: 20px 15px;
    .title-bar-title {
      text-align: center;
      width: 80%;
      float: left;
      .logo {
        display: inline-block;
        a {
          display: inline-block;
          width: 47px;
          height: 22px;
          background-image: url("../img/vector/raicom-logo.svg");
          background-size: 47px 22px; } } }
    .menu-icon {
      border: none;
      width: 25px;
      height: 20px;
      padding: 0;
      float: left;
      margin: 0;
      &:after {
        width: 25px;
        height: 20px;
        background-size: 25px 20px;
        background-image: url('../img/vector/ico-menu.svg');
        background-color: transparent;
        box-shadow: none; } } }
  .off-canvas {
    background-color: #002248;
    bottom: 0;
    color: white;
    font-size: 20px;
    font-weight: 300;
    .menu {
      width: 250px;
      margin-top: 60px; } }
  .search {
    float: right;
    position: static;
    padding: 0!important;
    a {
      padding: 0!important;
      &:before {
        margin-top: 0px;
        content: "";
        display: inline-block;
        background-image: url("../img/vector/search-mob.svg");
        background-size: 18px 18px;
        width: 18px;
        height: 18px;
        cursor: pointer; }
      &:after {
        content: normal!important; } } }
  .menu {
    >li {
      display: block;
      padding: 0 15px;
      a {
        color: white;
        font-size: 20px;
        font-weight: 300; } } }
  #menu-header-menu {
    width: 250px;
    margin-top: 60px;
    li {
      padding: 0 15px; } }
  .is-dropdown-submenu {
      background-color: #002248;
      list-style: none;
      border: none;
      position: fixed;
      width: 250px;
      top: 0!important;
      left: 0!important;
      bottom: 0!important;
      margin: 0;
      padding: 60px 14px 0 30px;
      .btn-back-to-menu {
        width: 15px;
        height: 16px;
        background: url("../img/vector/ico-back.svg");
        background-size: 15px 16px;
        position: absolute;
        top: 20px;
        left: 15px; } }
  .hide-submenu {
    display: none; }
  .dropdown.menu>li.is-dropdown-submenu-parent {
    .mob-submenu-arrow {
      border: none;
      width: 8px;
      height: 16px;
      background: url("../img/vector/mobile-menu-arrow.svg");
      background-size: 8px 16px;
      top: 13px;
      position: absolute;
      right: 15px; }
    >a:after {
      content: normal; } }
  .close-button {
    border: none;
    color: white;
    padding: 0;
    width: 100%;
    text-align: left;
    padding: 0 30px;
    &:hover {
      background: none; } }
  .off-canvas.position-left {
    left: -250px;
    top: 0;
    width: 100%; }
  .is-open-left {
    -webkit-transform: translateX(250px);
    transform: translateX(250px); }

  @media screen and (max-width: 640px) and (orientation: landscape) {
    .page-template-businnes #header {
      background-size: auto 100% !important;
      height: 80vh; }
    .external {
      #header {
        background-size: auto 100% !important;
        height: 80vh;
        .quote {
          top: 35vh; } } } }
