@media screen and (min-width: 641px) and (max-width: 1024px) {
  #header .top-bar ul li.is-dropdown-submenu-parent a {
    padding: 0 18px 0 10px;
    font-size: 9px; }
  .owl-controls {
    .owl-buttons {
      display: none; } }
  .owl-carousel {
    .owl-item {
      margin: 0 5px; } }
  .home {
    #video {
      height: auto; }
    #business {
      margin: 0;
      z-index: 0; }
    #shop {
      margin: 0; }
    #sales {
      height: auto;
      padding-bottom: 200px; } }
  #italian {
    position: relative; }
  #sales {
    display: none; }
  .page-template-businnes {
    section#two-columns {
      .text {
        padding: 0 5px; } } }
  .page-template-about_us {
    section#history {
      background-size: cover!important; } }
  .page-template-sub-project {
    section#preview {
      .browser {
        background-size: contain;
        height: auto;
        margin: 0 auto;
        background-repeat: no-repeat;
        img {
          margin: 0!important;
          padding: 33px 23px 0; } } }
    section.icon-list {
      a {
        margin: 0 44px; } }
    section#two-columns {
      .text {
        padding: 0 5px; } } }
  .external {
    .breadcrumbs {
      padding: 18px 15px 0; } }
  //MENU
  #header .top-bar .container {
    padding: 0; }
  #header .top-bar .dropdown.menu {
    font-size: 10px; }
  #header .top-bar ul a {
    padding: 0; }
  #footer {
    height: auto;
    .container {
      .top {
        .social-links {
          margin: 0 0 15px;
          width: 100%; }
        .acreditation {
          padding: 9px 12px; }
        .subscribe {
          margin: 0 15px; } } } } }
